<template>
  <div class="index-bg">
    <Header title="">
      <template #leftTitle>
        <img src="@/assets/images/logo.png" @error="$setErrorImg" alt="">
        BACK
      </template>
    </Header>
    <van-swipe class="index-swipe" indicator-color="white">
      <van-swipe-item v-for="(item,index) in dataImage[local].banner" :key="index"><img :src="item.image" alt="" /></van-swipe-item>
    </van-swipe>
    <div class="notice-box flex-middle" @click="$router.push({name:'Notice'})">
      <div class="flex-middle left-div">
        <img class="notice-img" src="@/assets/images/notice.png" />
      </div>
      <div class="flex-item">
        <van-swipe :autoplay="3000" class="notice-height" style="height:23px;" vertical :show-indicators="false">
          <van-swipe-item class="notice-text flex-middle" v-for="(item,index) in noticeDatas" :key="index">
            <span class="text-ellipsis flex-item">{{item[local].title}}</span>
            <!-- <img class="icon" src="@/assets/images/arr-r@2x.png" alt=""> -->
          </van-swipe-item>
          <van-swipe-item class="notice-text flex-middle" v-if="!noticeDatas.length">
            <span class="text-ellipsis flex-item" style="color: #969799;">{{t('index.暫無公告')}}</span>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <div class="card-wrap">
      <div class="item" @click="$router.push({name:'Ido0'})">
        <div class="img-wrap">
          <img src="@/assets/images/index1.png" alt="">
        </div>
        <div class="text">{{t('index.超級節點')}}</div>
      </div>
      <a class="item" href="https://aboutusback.github.io/threekingdoms">
        <div class="img-wrap">
          <img src="@/assets/images/index2.png" alt="">
        </div>
        <div class="text">{{t('index.三國演義')}}</div>
      </a>
      <a class="item" href="https://aboutusback.github.io">
        <div class="img-wrap">
          <img src="@/assets/images/index3.png" alt="">
        </div>
        <div class="text">{{t('index.足球世界杯')}}</div>
      </a>
      <div class="item">
        <div class="img-wrap">
          <img src="@/assets/images/index4.png" alt="">
        </div>
        <div class="text">{{t('index.DAO')}}</div>
      </div>
    </div>
    <Footer2></Footer2>
  </div>
</template>
<script>
import Header from '@/components/Header'
import Footer2 from '@/components/Footer2'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang } from "@/utils/i18n";
import notice from "@/utils/notice.json"
export default {
  components: {
    Header,
    Footer2
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      local: getLang() || "zh-TW",
      dataImage: {
        'zh-TW': { banner: [{ image: require('@/assets/images/banner_tw@2x.png') }] },
        'en': { banner: [{ image: require('@/assets/images/banner_en@2x.png') }] }
      },
      noticeDatas: notice,
      initContract: () => {
        data.initData()
      },
      initData: () => {
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.index-bg {
  min-height: 100vh;
  padding: 56px 16px 106px;
  position: relative;
  .card-wrap {
    display: flex;
    flex-wrap: wrap;
    padding-top: 13px;
    .item {
      width: calc(50% - 6.5px);
      padding: 33px 10px 19px;
      background: #1a202e;
      border-radius: 8px;
      margin-bottom: 12px;
      &:nth-child(odd) {
        margin-right: 13px;
      }
      .img-warp {
        height: 90px;
      }
      img {
        width: auto;
        height: 90px;
        display: flex;
        margin: 0 auto;
      }
      .text {
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
        font-size: 14px;
        text-align: center;
        padding-top: 41px;
      }
    }
  }
}
.index-swipe {
  .van-swipe-item {
    width: 100%;
    height: 154px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}
.notice-box {
  margin-top: 12px;
  background: #21283a;
  border-radius: 8px;
  padding: 5px 11px;
  .left-div {
    margin-right: 13px;
    .notice-img {
      width: 23px;
      height: 23px;
    }
  }
  .notice-height {
    .notice-text {
      font-size: 13px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #ffffff;
      .icon {
        width: 7px;
        height: 13px;
        display: block;
      }
    }
  }
}
</style>
