<template>
  <div class="footer2" @click="openVConsole()">
    <div class="media-wrap">
      <div class="slogn">BACK<br> {{t('index.區塊趨勢，勢不可擋')}}</div>
      <div class="media">
        <a href="https://twitter.com/back_btc">
          <img src="@/assets/images/media/1.png" @error="$setErrorImg" alt="">
        </a>
        <a href="javascript:void(0);"><img src="@/assets/images/media/2.png" @error="$setErrorImg" alt=""></a>
        <a href="javascript:void(0);"><img src="@/assets/images/media/3.png" @error="$setErrorImg" alt=""></a>
        <a href="javascript:void(0);"><img src="@/assets/images/media/4.png" @error="$setErrorImg" alt=""></a>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import Clipboard from "clipboard";
import { Toast } from "vant";

export default {
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      t: t,
      timer: null,
      times: 0,
      initWallet: () => {
        web3Data.initWeb3()
      },
      initData: () => {
      },
      openVConsole () {
        data.times++
        if (data.timer) {
          clearTimeout(data.timer)
          data.timer = null
        }
        data.timer = setTimeout(() => {
          data.times = 0
        }, 1000)
        if (data.times > 6) {
          console.log('已开启vConsole')
          data.times = 0
          sessionStorage.setItem('vConsole', 'show')
          window.history.go(0)
        }
      }
    })
    if (data.address) {
      data.initData();
    }
    onMounted(() => {
      var clipboard = new Clipboard(".address-copy");
      clipboard.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    watch(() => data.address, (val) => {
      if (val) {
        data.initData()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.footer2 {
  max-width: 640px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 16px 18px;
  margin: 0 auto;
  box-sizing: border-box;
  .media-wrap {
    font-size: 13px;
    font-family: PingFang SC;
    font-weight: bold;
    color: #ffffff;
    text-align: center;
    .media {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: center;
      padding-top: 12px;
      a {
        &:not(:last-child) {
          margin-right: 20px;
        }
      }
      img {
        width: 23px;
        height: 23px;
      }
    }
  }
}
</style>
